import React from 'react';
import { Menu, Badge } from 'antd';
import {Link} from  'react-router-dom'
//import {useContext} from 'react';
//import {UserContext} from '../../../context/app.context.js'
import {ShoppingCartOutlined} from '@ant-design/icons'
import {API_URL} from '../../../config'
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { UploadOutlined } from '@ant-design/icons';

function RightMenu(props) {
  //const {user} = useContext(UserContext)
  const navigate = useNavigate()

  const logoutHandler = async () => {
     await axios.get(`${API_URL}/logout`).then(response => {
      if (response.status === 200) {
        navigate("/signin");
      } else {
        alert('Log Out Failed')
      }
    });
  };

//   const handleLogout = async () => {
//     await axios.post(`${API_URL}/logout`, {}, {withCredentials: true})
//     setUser(null)
// }
  console.log("in right menu", props.user)

  if (!props.user) {
    return (
      <Menu mode={props.mode}>
        <Menu.Item key="signin">
          <Link to="/signin">Signin</Link>
        </Menu.Item>
        <Menu.Item key="signup">
          <Link to="/signup">Signup</Link>
        </Menu.Item>
      </Menu>
    )
  } else {
    return (
      <Menu mode={props.mode}>

        <Menu.Item key="history">
          <Link to="/history">History</Link>
        </Menu.Item>

        <Menu.Item key="upload">
          <Link to="/product/upload"><UploadOutlined /></Link>
        </Menu.Item>

        <Menu.Item key="cart" style={{ paddingBottom: 3 }}>
          <Badge count={5}>
            <Link to="/user/cart" style={{ marginRight: -22 , color:'#667777'}} >
              <ShoppingCartOutlined style={{ fontSize: 30, marginBottom: 3 }}/>
            </Link>
          </Badge>
        </Menu.Item>


        <Menu.Item key="logout">
          <Link to="/signin" onClick={logoutHandler}>Logout</Link>
        </Menu.Item>
      </Menu>
    )
  }
}

export default RightMenu;
